<template>
  <modal>
    <template slot="title">
      <span> </span>
    </template>
    <template slot="close">
      <span class="close" @click="closeModal">X</span>
    </template>
    <template slot="content">
      <div v-if="tradeList.length < 1 && mercList.length < 1">
        <h1>    .</h1>
      </div>
      <div v-if="tradeList.length >= 1 && typeSelect == 1">
        <div class="content-table">
          <h1 class="total_cnt">
             - {{ serverName }}  {{ tradeList.length }} 
          </h1>
          <canvas id="minmaxCanvas" style="height:100px"></canvas>
          <item :tradeList="tradeList" />
        </div>
      </div>
      <div v-if="mercList.length >= 1 && typeSelect == 3">
        <div class="content-table">
          <h1> - {{ serverName }} {{ mercList.length }}  </h1>
          <merc :mercList="mercList" />
        </div>
      </div>
      <div v-if="typeSelect == 2" class="flex-box">
        <div
          class="content-table"
          v-if="tradeList.length >= 1"
          :class="{ both: mercList.length > 0 }"
        >
          <h1 class="total_cnt">
             - {{ serverName }}  {{ tradeList.length }}  
          </h1>
          <item :tradeList="tradeList" />
        </div>
        <div
          class="content-table"
          v-if="mercList.length >= 1"
          :class="{ both: tradeList.length > 0 }"
        >
          <h1> - {{ serverName }} {{ mercList.length }}  </h1>
          <merc :mercList="mercList" />
        </div>
      </div>
    </template>
  </modal>
</template>

<script>
import modal from '@/components/common/modal';
import item from '@/components/common/item';
import merc from '@/components/common/merc';
import chartData from '@/util/chart';
import { getTradeListAvg } from '@/api/trade';

import Chart from 'chart.js';
export default {
  props: ['tradeList', 'typeSelect', 'serverSelect', 'inputData', 'mercList'],
  data() {
    return {
      serverName: '',
      chartData: chartData,
      itemData: null,
    };
  },
  components: {
    modal,
    item,
    merc,
  },
  async mounted() {
    switch (this.serverSelect) {
      case '1':
        this.serverName = '';
        break;
      case '2':
        this.serverName = '';
        break;
      case '3':
        this.serverName = '';
        break;
      case '4':
        this.serverName = '';
        break;
      case '5':
        this.serverName = '';
        break;
      case '6':
        this.serverName = '';
        break;
      case '7':
        this.serverName = '';
        break;
      case '8':
        this.serverName = '';
        break;
      case '9':
        this.serverName = '';
        break;
      case '10':
        this.serverName = '';
        break;
      case '11':
        this.serverName = '';
        break;
      case '12':
        this.serverName = '';
        break;
      case '13':
        this.serverName = '';
        break;
    }

    if (this.typeSelect == 1) {
      let { data } = await getTradeListAvg({
        server: this.serverSelect,
        inputData: this.inputData,
        type: 1,
      });
      this.itemData = data;
    }

    if (this.typeSelect == 1 && this.itemData[0].hasOwnProperty('series')) {
      this.itemData[0].series.reverse();
      this.createChart('minmaxCanvas', this.chartData);
    }
  },
  methods: {
    closeModal() {
      this.$emit('closeModal');
    },
    numberWithCommas(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    },
    createChart(charId, chartData) {
      const ctx = document.getElementById(charId);
      const myChart = new Chart(ctx, {
        type: chartData.type,
        data: chartData.data,
        options: chartData.options,
      }); //   

      myChart.data.datasets[0].data = this.itemData[0].series
        .slice(0, 10)
        .reverse()
        .map(x => x.value);
      myChart.data.labels = this.itemData[0].series
        .slice(0, 10)
        .reverse()
        .map(x => x.time);
      myChart.update();
    },
  },
};
</script>
<style scoped src="@/assets/css/modal.css"></style>
